.Console {
	width: 100%;
	height: 75%;
	display: grid;
	grid-template: 1fr 9fr/ 1fr;
	grid-template-areas: 'header' 'content';
	border-radius: 5px;
	font-size: .8rem;
	background: $background-white;

	&__header {
		grid-area: header;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		align-items: center;
		justify-content: space-between;
		border-top-right-radius: inherit;
		border-top-left-radius: inherit;
		background-color: $color-gray;
	}

	&__headerText {
		justify-self: center;
		font-size: .5rem;
		color: $color-white;
	}

	&__butons {
		display: flex;
	}

	&__btn {
		width: .8em;
		height: .8em;
		margin-left: .3rem;
		border-radius: 50%;

		&.close {
			background-color: red;
		}
		&.minimize {
			background-color: rgb(247, 215, 35);
		}
		&.maximize {
			background-color: rgb(149, 231, 18);
		}
	}

	&__content {
		color: $color-primary;
		padding: .5rem;
		overflow-y: scroll;
		overflow-x: hidden;
	}

	&__line {
		display: flex;

		&::before {
			color: $color-gray;
			content: 'david@int:~ ';
			margin-right: .5rem;
			opacity: .7;
		}

		&:last-child {
			&::after {
				content: '';
				width: .2rem;
				height: .8rem;
				margin-left: .1rem;
				background-color: $color-primary;
				animation: cursor .5s ease none infinite alternate;
			}
		}
	}
}

@keyframes cursor {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}
