.Home {
	@extend %page;
	@extend %scroll-transition;
	display: grid;
	grid-template: repeat(3, 1fr) /100%;
	justify-content: center;
	align-items: center;
	padding: .5rem;
	box-sizing: border-box;
	color: $color-black;
	position: relative;

	&__container {
		grid-row-start: 2;
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
	}

	&__title {
		width: 100%;
		font-size: 2.5rem;
		font-weight: bold;
		display: flex;
		justify-content: center;
	}

	&__letter {
		min-width: .25rem;
		transition: ease-in .5s;

		&.is-active {
			color: $color-primary;
		}
	}

	&__subtitle {
		display: flex;
		font-size: 1.5rem;
	}

	&__activitie {
		color: $color-primary;
		font-weight: bold;
		margin-left: .5em;
	}

	&__arrow {
		grid-row-start: 3;
		justify-self: end;
		align-self: end;
		margin: 0 1rem 1rem 0;
		font-size: 2rem;
		color: $color-primary;
		cursor: pointer;
		opacity: 1;
		transition: ease-in .2s;
		text-shadow: -3px -3px 3px rgba(255, 255, 255, 1), 3px 3px 3px rgba(0, 0, 0, .5);

		&:hover {
			opacity: 1;
			transform: translateX(5px);
		}
	}

	&::after {
		grid-row-start: 1;
		width: 25%;
		height: 50%;
		border: 5px solid $color-primary;
		position: relative;
		top: 100%;
		left: 35%;
		z-index: -1;
		content: '';
		animation: rectangle 24s linear infinite;
		box-shadow: -3px -3px 3px rgba(255, 255, 255, 1), 3px 3px 3px rgba(0, 0, 0, .5);
	}
}

@keyframes back {
	from {
		opacity: 0;
	}
	to {
		opacity: .2;
	}
}

@keyframes rectangle {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@media (max-width: 600px) {
	.Home {
		&__title {
			font-size: 1.3rem;
		}

		&__subtitle {
			font-size: 1rem;
		}
	}
}
