.Portfolio {
	@extend %page;
	@extend %scroll-transition;
	display: grid;
	grid-template: repeat(4, 23%) 4%/ 1fr 1fr;
	gap: .5rem 1rem;
	justify-content: center;
	align-items: top;
	padding: 1rem;
	color: $color-secondary;
	font-size: 4rem;
	font-weight: bold;
	box-sizing: border-box;
	overflow: scroll;
	background-color: $background-white;

	&__arrow {
		grid-row-start: 5;
		justify-self: start;
		align-self: center;
		font-size: 2rem;
		color: $color-primary;
		cursor: pointer;
		opacity: 1;
		transition: ease-in .2s;
		text-shadow: -3px -3px 3px rgba(255, 255, 255, 1), 3px 3px 3px rgba(0, 0, 0, .5);

		&:hover {
			opacity: 1;
			transform: translateX(-5px);
		}
	}
}

@media (max-width: 800px) {
	.Portfolio {
		grid-template: auto/ 1fr;
		row-gap: .5rem;

		& .Card {
			justify-self: center;
			width: 985%;
			height: 18vh;
			font-size: .6rem;
		}
	}
}
