.About {
	@extend %page;
	@extend %scroll-transition;
	display: grid;
	grid-template: repeat(2, 48%) 4%/ 1fr 1fr;
	grid-template-areas: "console skills" "cont cont" 'arrowL arrowR';
	justify-content: center;
	align-items: start;
	font-size: 4rem;
	color: $color-secondary;
	font-weight: bold;
	padding: 1rem;
	box-sizing: border-box;
	overflow: hidden;
	background-color: $background-black;

	& .Console {
		grid-area: console;
		justify-self: center;
		align-self: center;
		width: 80%;
		box-shadow: -3px -3px 3px rgb(65, 64, 64), 3px 3px 3px rgba(0, 0, 0, 1);
		position: relative;

		&__header {
			background-color: $color-gray;
		}

		&::after {
			width: 90%;
			height: 90%;
			border: 5px solid $color-primary;
			position: absolute;
			top: 0%;
			left: 5%;
			z-index: -1;
			content: '';
			animation: rectangle 24s linear infinite;
			box-shadow: -3px -3px 3px rgb(65, 64, 64), 3px 3px 3px rgba(0, 0, 0, 1);
		}
	}

	&__skills {
		grid-area: skills;
		align-self: center;
		display: grid;
		align-items: start;
		justify-self: center;
		grid-template: auto/ repeat(2, 1fr);
		gap: .5rem;
		font-size: .7rem;
		padding: 1rem 1rem;
		box-sizing: border-box;
	}

	&__skill {
		@extend %About-animate-grow;
		height: 100%;
		color: $color-white;
		padding: .3rem 1rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		opacity: 0;
		transform: translateX(-200px) scale(0.1);
		border: 1px solid $color-primary;
		border-radius: 5px;
		box-shadow: -3px -3px 3px rgb(65, 64, 64), 3px 3px 3px rgba(0, 0, 0, 1);
		box-sizing: border-box;
	}

	&__dots {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-left: 1rem;
	}

	&__dot {
		width: .8rem;
		height: .8rem;
		border-radius: 50%;
		margin-left: .2rem;
		position: relative;
		box-shadow: -3px -3px 3px rgb(65, 64, 64), 3px 3px 3px rgba(0, 0, 0, 1);

		&.fill {
			background-color: $color-primary;
		}

		&.empty {
			border: .1rem solid $color-primary;
			box-sizing: border-box;
		}

		&.half {
			border: .1rem solid $color-primary;
			box-sizing: border-box;
			overflow: hidden;

			&::before {
				content: '';
				position: absolute;
				width: inherit;
				height: inherit;
				top: 0;
				right: 50%;
				background-color: $color-primary;
			}
		}
	}

	&__container {
		@extend %About-animate-grow-container;
		grid-area: cont;
		align-self: center;
		justify-self: center;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		width: 70%;
		min-height: 70%;
		padding: 1rem;
		font-size: 1rem;
		text-align: center;
		border-radius: 5px;
		border: 1px solid $color-primary;
		box-shadow: -3px -3px 3px rgba(64, 65, 64, 1), -3px 3px 3px rgba(0, 0, 0, .5);
		background-color: transparent;
		animation-delay: 5s;
	}

	&__description {
		@extend %About-animate-grow;
		width: 100%;
		height: 100%;
		color: $color-white;
		font-weight: 300;
		opacity: 0.8;
		padding: 1em;
	}

	&__photo {
		@extend %About-animate-grow;
		max-width: 50%;
		max-width: 25vw;
		height: 100%;
		border-radius: 5px;
		transform: scaleX(-1);
		margin-bottom: 1em;
		box-shadow: 3px -3px 3px rgba(255, 255, 255, .6), -3px 3px 3px rgba(0, 0, 0, .5);
		filter: grayscale(.8) contrast(.9);
	}

	&__arrow {
		font-size: 2rem;
		color: $color-primary;
		cursor: pointer;
		opacity: .5;
		padding: 0 1rem;
		text-shadow: -3px -3px 3px rgb(65, 64, 64), 3px 3px 3px rgba(0, 0, 0, 1);

		&.left {
			grid-area: arrowL;
			justify-self: start;
			&:hover {
				opacity: 1;
				transform: translateX(-5px);
			}
		}

		&.right {
			grid-area: arrowR;
			justify-self: end;
			&:hover {
				opacity: 1;
				transform: translateX(5px);
			}
		}
	}
}

%About-animate-grow {
	animation: grow 1s ease-in 1 forwards;
}

%About-animate-grow-container {
	animation: container 1s ease-in 1 5s forwards;
}

@keyframes grow {
	from {
		opacity: 0;
		transform: translateY(20px) scale(0);
	}
	to {
		opacity: 1;
		transform: translateY(0) scale(1);
	}
}
@keyframes container {
	from {
		background-color: transparent;
	}
	to {
		// background-color: $color-white;
	}
}

@media (max-width: 800px) {
	.About {
		justify-content: center;
		grid-template: 38% 20% 35% 5%/ 50% 50%;
		grid-template-areas: "console console""cont cont " "skills skills " "arrowL arrowR";

		& .Console {
			font-size: .4rem;
		}

		&__skills {
			font-size: .5rem;
			align-self: center;
		}

		&__container {
			padding: .5rem;
		}

		&__photo {
			height: 100%;
			max-height: 10vh;
			margin: 0;
		}

		&__description {
			font-size: .4rem;
			padding: .3rem;
		}

		&__arrow {
			margin-top: .5rem;
			font-size: 1.5rem;
		}
	}
}
