.Header {
	width: 100%;
	height: 100%;
	grid-area: header;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: .5rem;
	box-sizing: border-box;

	&__logo,
	&__logoimg {
		height: 40px;
		border-radius: 5px;
		background-color: $color-primary;
		// box-shadow: -4px -3px 4px rgba(255, 255, 255, .8), 3px 3px 5px rgba(0, 0, 0, .5);
	}

	&__menu {
		display: flex;
		color: $color-primary;
	}

	&__item {
		list-style-type: none;
		margin: 0 .4rem;
		padding: .25rem .5rem;
		border-radius: 5px;
		border: 1px solid $color-primary;
		box-shadow: -4px -3px 4px rgba(255, 255, 255, .8), 3px 3px 5px rgba(0, 0, 0, .5);
		transition: ease .5s;
		overflow: hidden;
		background-color: white;

		&:hover {
			box-shadow: none;
		}
	}

	&__nav {
		text-decoration: none;
		color: $color-black;
		font-weight: 300;
		outline: none;
		position: relative;
		z-index: 20;

		&.is-active {
			color: $color-primary;
		}

		&:hover::before {
			opacity: 0.7;
		}

		&::before {
			content: '';
			width: 1.5rem;
			height: 1.5rem;
			background-color: $color-primary;
			position: absolute;
			border-radius: 50%;
			opacity: 0;
			top: calc(50% - .75rem);
			left: calc(50% - .75rem);
			z-index: 10;
			transition: ease-in-out .2s;
		}
	}
}

@media (max-width: 600px) {
	.Header {
		&__item {
		}
	}
}
