@mixin Transition-shadow($x: -5px, $color: rgba(0, 0, 0, 0.2)) {
	box-shadow: $x 0px 15px 0px $color;
}

.Transition {
	@extend %page;
	@extend %scroll-transition;
	@include Transition-shadow();
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
	z-index: 10;
	color: $color-white;
	background-color: transparent;
	transform: translateX(100%);
	overflow: hidden;

	&__text {
		width: 100%;
		text-align: center;
		font-size: 5rem;
		position: relative;
		bottom: 0%;
		text-shadow: -4px -2px 3px rgba(255, 255, 255, 0.2), 4px 4px 4px rgba(0, 0, 0, 0.5);
		z-index: 20;
	}

	&__bar {
		@extend %scroll-transition;
		width: 100%;
		height: 5px;
		position: absolute;
		bottom: 0px;
		z-index: 20;
		background-color: $color-primary;
	}

	&.white {
		@include Transition-shadow(-5px, rgba(255,255,255,0.2));

		&::before {
			background-color: $background-white;
		}

		&.left {
			@include Transition-shadow(5px, rgba(255,255,255,0.2));

			&::before {
				@include Transition-shadow(5px, rgba(0,0,0,0.5));
			}
		}
	}

	&.left {
		transform: translateX(-100%);
		@include Transition-shadow(5px, rgba(255,255,255, .2));

		&::before {
			left: -20px;
		}
	}

	&::after {
		@extend %page;
		content: '';
		background-color: $color-primary;
		position: absolute;
		top: 0;
		z-index: 10;
	}

	&::before {
		@extend %page;
		height: 150vh;
		content: '';
		background-color: $color-black;
		position: absolute;
		top: 0;
		left: 20px;
		transform: rotateZ(-2deg) translate(10px, -5rem);
		z-index: 20;
		@include Transition-shadow(-5px, rgba(0,0,0,0.5));
	}
}
