.Demo {
	@extend %page;
	@extend %scroll-transition;
	display: grid;
	grid-template: 5% 90% 5%/ 1fr;
	gap: .5rem 1rem;
	justify-content: center;
	align-items: top;
	padding: 1rem;
	color: $color-secondary;
	font-size: 4rem;
	font-weight: bold;
	box-sizing: border-box;
	overflow: scroll;
	background-color: $background-white;

	&__title {
		justify-self: center;
		color: $color-primary;
		font-size: 1.5rem;
		text-decoration: none;
		text-transform: capitalize;
	}

	&__app {
		width: 100%;
		height: 100%;
		grid-row-start: 2;
		overflow: hidden;

		iframe {
			width: 100%;
			height: 100%;
		}
	}

	&__arrow {
		grid-row-start: 3;
		justify-self: start;
		align-self: end;
		font-size: 2rem;
		color: $color-primary;
		cursor: pointer;
		opacity: 1;
		transition: ease-in .2s;
		text-shadow: -3px -3px 3px rgba(255, 255, 255, 1), 3px 3px 3px rgba(0, 0, 0, .5);

		&:hover {
			opacity: 1;
			transform: translateX(-5px);
		}
	}
}
