.Card {
	$super: &;

	display: flex;
	max-width: 500px;
	max-height: 200px;
	position: relative;
	overflow: hidden;
	border-radius: 5px;
	font-size: 1rem;
	text-decoration: none;
	border: 1px solid $color-primary;
	cursor: pointer;
	box-shadow: -5px -4px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, .5);

	&__img {
		@extend %card-transition;
		width: 80%;
		box-sizing: border-box;
		filter: grayscale(1);
	}

	&__side {
		width: 20%;
		padding: .5em;
		box-sizing: border-box;
		text-align: start;
		background-color: white;
		position: relative;
		overflow: hidden;

		&::before {
			@extend %card-transition;
			content: '';
			width: 2em;
			height: 2em;
			background-color: $color-primary;
			border-radius: 50%;
			position: absolute;
			right: 8%;
			bottom: 5%;
		}
	}

	&__title {
		@extend %card-transition;
		position: relative;
		z-index: 10;
		margin: .5em 0 0 .5em;
		color: $color-gray;
		font-size: 1em;
		writing-mode: vertical-lr;
	}

	&__link {
		font-size: 2em;
		position: absolute;
		bottom: 5%;
		left: -2%;
		z-index: 100;
		color: $color-white;
		opacity: 0;
	}

	&:hover {
		#{$super}__side::before {
			transform: scale(15);
		}

		#{$super}__title {
			color: $color-white;
		}

		#{$super}__img {
			filter: none;
		}

		#{$super}__link {
			opacity: 1;
		}
	}

	&:nth-child(2n) &__side::before {
		background: $color-secondary;
	}

	// &:nth-child(3n) &__side::before {
	// 	background: $color-primary;
	// }

	// &::before {
	// 	@extend %card-dot;
	// 	top: -.5rem;
	// }

	// &::after {
	// 	@extend %card-dot;
	// 	bottom: -.5rem;
	// }
}

%card-dot {
	content: '';
	width: 1rem;
	height: 1rem;
	box-sizing: border-box;
	position: absolute;
	right: calc(20% - .5rem + 1.5px);
	z-index: 100;
	border-radius: 50%;
	border: 1px solid white;
	background-color: $color-white;
}

%card-transition {
	transition: all 0.3s ease;
}
